import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
import mutations from './mutations'
//Vuex
Vue.use(Vuex)

//VueX
const store = new Vuex.Store({
    state:{
      //
      global:[]
    },
    actions,
    getters,
    mutations,
})

export default store



